import { defHttp } from '/@/utils/http/axios';
import { BasicColumn } from '/@/components/Table';
import { useRender } from '@/components/Table';
import type { FormProps } from '/@/components/Form/src/types/form';
import { useUserStore } from '/@/store/modules/user';

export enum Api {
  LIST_STATION_TREE_URL = '/baseset/common/list-orgStationTree',
  LIST_STATION_PERSON_URL = '/baseset/common/list-stationPerson',
  LIST_ORG_TREE = '/system/org/list-tree',
  CREATE_URL = '/po/ticket/operationticket/create',
  UPDATE_URL = '/po/ticket/operationticket/update',
  DELETE_URL = '/po/ticket/operationticket/delete',
  UPLOAD_FILE_URL = '/po/ticket/operationticket/upload-file',
  EXPORT_EXCEL_URL = '/po/ticket/operationticket/export-excel',
  GET_URL = '/po/ticket/operationticket/get',
  PAGE_URL = '/po/ticket/operationticket/page',
  LIST_ITEM_URL = '/po/ticket/operationticket/item/list',
  GET_ORG_URL = '/po/ticket/operationticket/org',

  IMPORT_EXCEL_URL = '/admin-api/po/ticket/operationticket/item/import-excel',
}

export const dateTimeFormat = 'YYYY-MM-DD HH:mm';
export const importExcelUrl = Api.IMPORT_EXCEL_URL + '?_t=' + new Date().getTime();

// 获取电站树
export const getTreeList = (params) =>
  defHttp.post(
    {
      url: Api.LIST_STATION_TREE_URL,
      params,
    },
    {},
  );

// 获取电站下的组织人员
export const getStationPerson = (params) =>
  defHttp.post(
    {
      url: Api.LIST_STATION_PERSON_URL,
      params,
    },
    {},
  );

// 获取组织树
export const getOrgTree = (params) =>
  defHttp.post(
    {
      url: Api.LIST_ORG_TREE,
      params,
    },
    {},
  );

// 添加操作票
export const createData = (params) =>
  defHttp.post(
    {
      url: Api.CREATE_URL,
      params,
    },
    {},
  );

// 修改操作票
export const updateData = (params) =>
  defHttp.put(
    {
      url: Api.UPDATE_URL,
      params,
    },
    {},
  );

export const saveData = (params) => {
  if (!params.id) {
    return createData(params);
  }
  return updateData(params);
};

// 删除操作票
export const deleteData = (params) =>
  defHttp.delete(
    {
      url: Api.DELETE_URL,
      params,
    },
    {},
  );

// 导出操作票
export const exportExcel = (params) =>
  defHttp.post(
    {
      url: Api.EXPORT_EXCEL_URL,
      params,
      responseType: 'blob',
    },
    { isOnlyResult: true },
  );

// 上传操作票文件
export const uploadFile = (params) =>
  defHttp.post(
    {
      url: Api.UPLOAD_FILE_URL,
      params,
    },
    {},
  );

// 获取操作票
export const getData = (params) =>
  defHttp.get(
    {
      url: Api.GET_URL,
      params,
    },
    {},
  );

// 分页获取操作票列表
export const getPage = (params) =>
  defHttp.get(
    {
      url: Api.PAGE_URL,
      params,
    },
    {},
  );

// 获取操作票内容列表
export const getItemList = (params) =>
  defHttp.get(
    {
      url: Api.LIST_ITEM_URL,
      params,
    },
    {},
  );

// 获取登录用户信息
export const getOrg = (params) =>
  defHttp.get(
    {
      url: Api.GET_ORG_URL,
      params,
    },
    {},
  );

export const columns = (): BasicColumn[] => {
  const result = [
    {
      title: '操作票号',
      dataIndex: 'ticketNumber',
      sorter: true,
      resizable: true,
      width: 200,
    },
    {
      title: '所属公司',
      dataIndex: 'stationOrganName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '电站名称',
      dataIndex: 'stationName',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '发令人',
      dataIndex: 'commanderName',
      sorter: true,
      resizable: true,
      width: 100,
    },
    {
      title: '受令人',
      dataIndex: 'recipientName',
      sorter: true,
      resizable: true,
      width: 100,
    },
    {
      title: '操作任务',
      dataIndex: 'subject',
      headAlign: 'center',
      align: 'left',
      sorter: true,
      resizable: true,
      width: 220,
    },
    {
      title: '发令时间',
      dataIndex: 'commandTime',
      sorter: true,
      resizable: true,
      width: 140,
      customRender: ({ text }) => {
        return useRender.renderDate(text, dateTimeFormat);
      },
    },
    {
      title: '操作时间',
      dataIndex: 'principalTime',
      sorter: true,
      resizable: true,
      width: 140,
      customRender: ({ text }) => {
        return useRender.renderDate(text, dateTimeFormat);
      },
    },
    {
      title: '完成时间',
      dataIndex: 'qualifiedTime',
      sorter: true,
      resizable: true,
      width: 140,
      customRender: ({ text }) => {
        return useRender.renderDate(text, dateTimeFormat);
      },
    },
    {
      title: '检查结论',
      dataIndex: 'result',
      sorter: true,
      resizable: true,
      width: 100,
      customRender: ({ record }) => {
        return record.resultText;
      },
    },
    {
      title: '检查时间',
      dataIndex: 'checkTime',
      sorter: true,
      resizable: true,
      width: 150,
    },
    {
      title: '创建人',
      dataIndex: 'personMemberName',
      sorter: true,
      resizable: true,
      width: 100,
    },
    {
      title: '创建时间',
      dataIndex: 'fillinDate',
      sorter: true,
      resizable: true,
      width: 150,
    },
    {
      title: '审批状态',
      dataIndex: 'status',
      sorter: true,
      resizable: true,
      width: 100,
      customRender: ({ text, record }) => {
        return useRender.renderFlowStatus(text, record.statusText);
      },
    },
  ];
  if (useUserStore().isMultiOrganization) {
    return result as BasicColumn[];
  } else {
    const [one, , ...others] = result;
    return [one, ...others] as BasicColumn[];
  }
};

// 操作内容
export const getItemColumns = (process) => {
  const columns: BasicColumn[] = [
    {
      title: '操作内容',
      dataIndex: 'content',
      headAlign: 'center',
      align: 'left',
      width: 220,
    },
    {
      title: '是否完成',
      dataIndex: 'status',
      width: 100,
      ifShow: process.operation.show,
    },
  ];
  return columns;
};

